<template>
  <div class="input-box">
    <input type="text" v-model="inputValue" placeholder="请输入你的脏话及一切关键词.."/>
    <div class="show-box">{{ outputString }}</div>
  </div>
</template>

<script>
import AntiKeyWords from 'anti-key-words'

export default {
  name: 'HelloWorld',
  data () {
    return {
      inputValue: "",
      outputString: ""
    }
  },
  watch: {
    inputValue: function (value, oldValue) {
      let inputValue = value
      let antiKeyWords = new AntiKeyWords(inputValue)
      // initialize antikeywords class
      antiKeyWords.filterKeyWords()
      console.log(antiKeyWords.outputString) // Hi, **
      this.outputString = antiKeyWords.outputString
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .input-box input {
    width: 400px;
    height: 60px;
    font-size: 24px;
    text-align: center;
  }
  .input-box .show-box {
    font-size: 38px;
    color: red;
  }
</style>



